"sourcesContent":["/* include the Font Awesome CSS */\n@import \"../../node_modules/font-awesome/less/font-awesome.less\";\n///////////////////////////////////////////////////////////////////////////////\n// global definitions \n@fjThemeColor:#FF0000 ;\n@fjProgressForeground: #8F9B9E;\n@fjProgressHeight:6px;\n@fjProgressBorder: #3C4447;\n@fjThemeFontFamily: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n@fjThemeFontSize: 12px;\n@fjThemeTextColor: #FFFFFF;\n@fjBackgroundPlayerColor : #000000;\n@fjBackgroundControlColor : rgba(0, 0, 0, 0.6);\n@fjBackgroundControlColorOnHover :#6c6c6c; // 545353\n@fjItemsOpacity:75;\n@fjItemsOpacityOnHover:100;\n@fjZindex:2147483647;\n@fjBorderRadius:5px;\n@fjOverlayAnnonceColor:rgb(119, 255, 119);\n@fjOverlayErrorColor:rgb(255, 0, 0);\n@fjOverlayLinkColor:rgb(255, 255, 0);\n@fjIconSize: 1.3em;\n////////////////////////////////////////////////////// -------------------  ICONS ------------------- /////////////////////////////////////////////\n@fjIconsFont: \"FontAwesome\";\n@fjIconsNames:leftarrow,download,share,play,playNext,playPrevious,pause,volUp,volDown,mute,audios,subs,fullScreen,compressScreen,windowClose;\n@fjIconsContents: \"\\f060\",\"\\f019\",\"\\f1e0\",\"\\f04b\", \"\\f051\",\"\\f048\", \"\\f04c\",\"\\f028\",\"\\f027\",\"\\f026\", \"\\f130\", \"\\f20a\", \"\\f0b2\",\"\\f066\",\"\\f00d\";\n.make-icons-classes(@i: length(@fjIconsNames)) when (@i > 0) {\n    .make-icons-classes(@i - 1);\n    @name: extract(@fjIconsNames, @i); \n    @code: extract(@fjIconsContents, @i); \n      .fj-icon-@{name} {\n         font-family: @fjIconsFont;\n         font-size: @fjIconSize;\n         content:@code;\n    }\n    .fj-icon-@{name}:before { content:@code; }\n}\n.make-icons-classes(); \n////////////////////////////////////////////////////// -------------------  OPACITY ------------------- //////////////////////////////////////////\n.opacity(@opacity) {\n  opacity: @opacity / 100;\n}\n///////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////\n/*---------- general & component -----------*/\n.fj-player {\n     margin: 0 auto;\n } \n .fj-figure {\n     background-color:@fjBackgroundPlayerColor;\n     color:@fjThemeTextColor;\n     text-align: center;\n     position: relative;\n     margin: 0px auto 0px auto;\n     display: inline-block;\n     font-family: @fjThemeFontFamily;\n     font-size:@fjThemeFontSize;     \n     width: 100%;\n }\n .fj-control {\n     background-color:@fjBackgroundControlColor;\n     color:@fjThemeTextColor;     \n     .opacity(@fjItemsOpacity);\n     cursor: pointer;\n }\n .fj-control-embd {     \n     color:@fjThemeTextColor;     \n     .opacity(@fjItemsOpacity);\n     cursor: pointer;\n }\n .fj-control-embd:hover,\n .fj-control:hover {\n     .opacity(@fjItemsOpacityOnHover);\n     background: @fjBackgroundControlColorOnHover;\n }\n.fj-video-progress {\n    margin-bottom: 0.5%;\n    margin-top: 0.5%;\n    padding-left: 1px;\n    padding-right: 1px;\n}\n/*---------- position Horizental -----------*/\n .divTspanitleSeparator {\n     position: relative;\n     visibility: visible;\n     text-overflow: ellipsis;\n     overflow: hidden;\n     margin: 0px;\n     white-space: nowrap;\n     display: inline-block;\n     max-width: 766px;\n     font-size: 12px;\n     padding: 10px;\n }\n .fj-horizental-top {\n     z-index:  @fjZindex;\n     border-radius: @fjBorderRadius;\n     top: 5%;\n     width: 80%;\n     position: absolute;\n     left: 10%;\n }\n .fj-horizental-center {\n     z-index:  @fjZindex;\n     border-radius: @fjBorderRadius;\n     top: 45%;\n     width: 80%;\n     position: absolute;\n     left: 10%;\n }\n .fj-horizental-bottomMenus {\n     z-index:  @fjZindex;\n     position: absolute;\n     border-radius: @fjBorderRadius;\n     bottom: 22%;\n     opacity: 0.8;\n     background: rgba(10, 10, 10, 0.8);\n     width: 90%;\n     margin-left: 5%;\n     display: relative;\n }\n .fj-horizental