radius: 5px;\n     border-top-right-radius: 5px;\n     border-bottom-right-radius: 5px;\n     border-bottom-left-radius: 5px;\n     padding-left: 10px;\n     padding-right: 10px;\n     padding-top: 5px;\n     padding-bottom: 5px;\n     text-overflow: ellipsis;\n     overflow: hidden;\n     white-space: nowrap;\n     display: inline-block;\n     font-size: 4em ;\n     margin-top: -15px;\n     margin-left: -15px;\n }\n.fj-title {\nbackground-color:@fjBackgroundControlColor;\n }\n .fj-hide {\n     visibility: hidden;\n     display : none;\n }\n .fj-overlayContainer {     \n     height: 100%;\n     margin: 0 auto;     \n }\n .fj-overlay {\n     position: absolute;\n     background: rgba(0, 0, 0, 0.5);\n     float: left;\n     font-size: 12px;\n     padding: 5px;\n     border-radius: @fjBorderRadius;\n     z-index:  @fjZindex;\n     top: 20%;\n     left: 20%;\n     height: auto;\n }\n .fj-adsinfo {}\n\n /* ----------------------- spinner  ------------------------ */\n \n @keyframes spinner {\n     to {\n         transform: rotate(360deg);\n     }\n }\n \n .spinner:before {\n     content: '';\n     box-sizing: border-box;\n     z-index:  @fjZindex;\n     position: absolute;\n     top: 50%;\n     left: 50%;\n     width: 30px;\n     height: 30px;\n     margin-top: -15px;\n     margin-left: -15px;\n     border-radius: 50%;\n     border: 3px solid @fjProgressForeground;\n     border-top-color: @fjThemeColor;\n     animation: spinner .6s linear infinite;\n }\n /*_________________________ RANGE ________________________*/\n \n .volumebar {\n     margin-top: 1%;\n     padding-left: 1%;\n     padding-right: 3%;\n     border-left: none;\n     border-right: none;\n }\n // Generic ======================================================\n input[type=\"range\"] {   \n     margin: auto;\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    position: relative;\n    overflow: hidden;\n    height: @fjProgressHeight ;\n    width: 98% !important;    \n    border-radius:  @fjBorderRadius;     \n    cursor: pointer;\n    margin-top: 2px;\n    margin-bottom: 2px;\n } \n input[type=range]:focus {\n     outline: none;\n }\n// WEBKIT ======================================================\n input[type='range']::-webkit-slider-runnable-track {\n    background: @fjProgressForeground;\n    border-radius:  @fjBorderRadius;     \n}\ninput[type='range']::-webkit-slider-thumb,\ninput[type='range']::-webkit-media-slider-thumb {\n    background:@fjProgressForeground;\n    box-shadow: -100vw 0 0 100vw @fjThemeColor;\n    border: 2px solid @fjProgressBorder ;\n    color: @fjThemeColor;\n    height: @fjProgressHeight;\n    border-radius: 100%;\n}\n // Mozilla ====================================================\ninput[type='range']::-moz-range-track {\n    background: @fjProgressForeground;\n    height: @fjProgressHeight ;\n    border-radius:  @fjBorderRadius;     \n}\ninput[type='range']::-moz-range-thumb {    \n    color: @fjThemeColor;\n    height: @fjProgressHeight;\n    border: 2px solid  @fjProgressBorder;\n    border-radius:  @fjBorderRadius;    \n    box-shadow: -100vw 0 0 100vw @fjThemeColor;\n}\n// IE ==========================================================\n/* todo \ninput[type=\"range\"]::-moz-range-progress {\n     background-color: #FF0000;\n}\ninput[type='range']::-ms-fill-upper {\n    background-color: @fjProgressForeground;\n}\ninput[type='range']::-ms-fill-lower {\n    background-color: @fjThemeColor;\n}*/\n/* animation */\n\n.m-fadeOut {\n    visibility: hidden;\n    opacity: 0;\n    transition: visibility 200ms, opacity 200ms;\n}\n\n.m-fadeIn {\n    visibility: visible;\n    opacity: 1;\n}\n /* ---------------------- Menu style --------------------- */\n .fj-list-title {\n     cursor: default;\n     font-weight: bold;\n     padding: 5px;\n }\n .settingMenuDiv {\n     position: absolute;\n     background: rgba(10, 10, 10, 0.8);\n     bottom: 10%;\n     left: 85%;\n     margin-bottom: 15px;\n     border-radius: @fjBorderRadius;\n     padding-right: 5px;\n     list-style-type: none;\n }\n .settingMenuDivnt div,\n .settingMenuDivnt ul {\n     display: inline;\n     list-style-type: none;\n }\n .fj-list {\n     list-style-type: none;\n     padding-left: 25px;\n }\n .settingMenuSubMenuRight {\n     float: left;\n     width: 100%;\n     border-left: 1px solid #2A2A2A;\n     padding-left: 5px;\n     padding-right: 5px;\n }\n .subtitles-menu-item-actif {\n     cursor: pointer;\n     text-align: left;\n     border-radius: @fjBorderRadius;\n     opacity: 1;\n }\n .subtitles-menu-item-actif:before {\n     content: \"\\f00c\";\n     /* FontAwesome Unicode */\n     font-family: FontAwesome;\n     display: inline-block;\n     margin-left: -1.3em;\n     /* same as padding-left set on li */\n     width: 1.3em;\n     /* same as padding-left set on li */\n }\n .subtitles-menu-item {\n     cursor: pointer;\n     text-align: left;\n     list-style-type: none;\n     opacity: 0.9;\n } \n /*------------------ OVERLAYS -------------*/\n .fj-overlays-container {\n     width: 100%;\n     height: 100%;\n     margin: 0 auto;\n     padding: 0px;\n     /*position: relative;*/\n     position: absolute;\n }\n .over-HL {\n     position: absolute;\n     background: rgba(0, 0, 0, 0.5);\n     float: left;\n     padding: 5px;\n     border-radius: @fjBorderRadius;\n     z-index:  @fjZindex;\n     /****/\n     top: 25%;\n     left: 20%;\n     height: auto;\n }\n\n .over-HL-error {\n     position: absolute;\n    background:rgba(255, 0, 0, 0.5);\n     float: left;\n     padding: 5px;\n     border-radius: @fjBorderRadius;\n     z-index:  @fjZindex;\n     /****/\n     top: 25%;\n     left: 20%;\n     height: auto;\n }\n \n .over-HR {\n     position: absolute;\n     background: rgba(0, 0, 0, 0.5);\n     float: left;\n     padding: 5px;\n     border-radius: @fjBorderRadius;\n     z-index:  @fjZindex;\n     /****/\n     top: 25%;\n     left: 80%;\n     height: auto;\n }\n .fj-overlay-closing {\n     float: right;\n     text-decoration: none;\n     display: inline-block;\n }\n .fj-overlay-annonce {\n     color: @fjOverlayAnnonceColor;\n     font-size: 1em;\n     float: left;\n     text-decoration: none;\n     display: inline-block;\n }\n .fj-overlay-error{\n     color: @fjOverlayErrorColor;\n     font-size: 1em;\n     float: left;\n     text-decoration: none;\n     display: inline-block;\n }\n .fj-overlay-link {\n     color: @fjOverlayLinkColor;\n     font-size: 0.8em;\n     float: left;\n     text-decoration: none;\n     display: inline-block;\n }\n .fj-overlay-description {\n     font-size: 1.2em;\n     float: right;\n     text-decoration: none;\n     display: inline-block;\n }\n /* ---------------------- Thumbs style --------------------- */\n .thumbsBlockDiv {\n     position: absolute;\n     margin-bottom: 15px;\n     padding-top: 5px;\n     border-radius: @fjBorderRadius;\n     bottom: 8%;\n     background: rgba(10, 10, 10, 0.8);\n } \n .thumbsBlock {\n     display: table-cell;\n     border-radius: @fjBorderRadius;\n     vertical-align: top;\n     display: inline-block;\n }\n/*=====================================================================================================*/\nfigure[controls-hidden=false] video::-webkit-media-text-track-display {\n    position: relative !important ; /*absolute*/\n    top:  80%  !important; /* 0 */     \n}\n/*\nvideo::cue {\n  // add style here\n}\n*/\nfigure[controls-hidden=false] video::-moz-media-text-track-display {\n    position: relative !important ; /*absolute*/\n    top:  80%  !important; /* 0 */     \n}\n/*_________________________________ FULL SCREEN __________________________*/\n\n\n/* fullscreen */\n\nhtml:-ms-fullscreen {\n    width: 100%;\n}\n\n :-webkit-full-screen {\n    background-color: transparent;\n}\n\n\n/* hide controls on fullscreen with WebKit */\n\nfigure[data-fullscreen=true] video::-webkit-media-controls {\n    display: none !important;\n}\nfigure[data-fullscreen=true] video::-moz-media-controls {\n    display: none !important;\n}\nfigure[data-fullscreen=true] {\n    max-width: 100%;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n}\nfigure[data-fullscreen=true] video {\n    height: 100%;\n}\nfigure[data-fullscreen=true] .volumebar {\n   width: 35%;\n}\nfigure[data-fullscreen=true] .fj-title {\n    font-size: 3em;\n}\nfigure[data-fullscreen=true] .fj-text {\n    font-size: 2em;\n}\nfigure[data-fullscreen=true] .fj-big-btn  {\n    font-size: 8em;\n}\nfigure[data-fullscreen=true] .fj-btn {\n    font-size: 3em;\n    margin-bottom: 5px;\n}\nfigure[data-fullscreen=true] .spinner::before {\n    width: 55px;\n    height: 55px;\n    border: 5px solid @fjProgressForeground;\n    border-top-color: @fjThemeColor;\n}\n","/* FONT PATH\n * -------------------------- */\n\n@font-face {\n  font-family: 'FontAwesome';\n  src: url('@{fa-font-path}/fontawesome-webfont.eot?v=@{fa-version}');\n  src: url('@{fa-font-path}/fontawesome-webfont.eot?#iefix&v=@{fa-version}') format('embedded-opentype'),\n    url('@{fa-font-path}/fontawesome-webfont.woff2?v=@{fa-version}') format('woff2'),\n    url('@{fa-font-path}/fontawesome-webfont.woff?v=@{fa-version}') format('woff'),\n    url('@{fa-font-path}/fontawesome-webfont.ttf?v=@{fa-version}') format('truetype'),\n    url('@{fa-font-path}/fontawesome-webfont.svg?v=@{fa-version}#fontawesomeregular') format('svg');\n  // src: url('@{fa-font-path}/FontAwesome.otf') format('opentype'); // used when developing fonts\n  font-weight: normal;\n  font-style: normal;\n}\n","/* Font Awesome uses the Unicode Private Use Area (PUA) to ensure screen\n   readers do not read off random characters that represent icons */\n\n.@{fa-css-prefix}-glass:before { content: @fa-var-glass; }\n.@{fa-css-prefix}-music:before { content: @fa-var-music; }\n.@{fa-css-prefix}-search:before { content: @fa-var-search; }\n.@{fa-css-prefix}-envelope-o:before { content: @fa-var-envelope-o; }\n.@{fa-css-prefix}-heart:before { content: @fa-var-heart; }\n.@{fa-css-prefix}-star:before { content: @fa-var-star; }\n.@{fa-css